.feedback {
  font-style: italic;
  font-weight: var(--font-weight-medium, 500);
  padding: 4px 0;
}

.feedback.is-valid {
  color: var(--color-success, #168821);
}

.feedback.is-invalid {
  color: var(--color-danger, #e60000);
}

.feedback.is-disabled {
  color: #555;
}

.br-divider {
  align-items: center;
  display: flex;
  justify-content: center;
  margin: 1em 0;
  border-top: 1px solid var(--color-secondary-04, #ccc);
  border-left: none;
  border-right: none;
  border-bottom: none;
  width: 100%;
}

.br-divider.fundo-neutro {
  border-top: 1px solid var(--color-secondary-01, #fff);
  margin: 1em 0;
  border-left: none;
  border-right: none;
  border-bottom: none;
  width: 100%;
}

.dropdown {
  position: relative;
  z-index: 1;
}

.dropdown .menu {
  background: var(--color-secondary-01, #fff);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.22);
  display: none;
  position: absolute;
}

.dropdown .menu .title {
  display: block;
  font-weight: var(--font-weight-semi-bold, 600);
  padding: 8px 16px;
}

.dropdown .menu .item {
  background: var(--color-secondary-01, #fff);
  border: 0;
  display: block;
  padding: 8px 16px;
  position: relative;
  text-align: left;
  width: 100%;
}

.dropdown .menu a.item,
.dropdown .menu button.item {
  text-decoration: none;
}

.dropdown .menu a.item:hover,
.dropdown .menu button.item:hover {
  background: var(--status-hover-background, var(--color-primary-pastel-02));
}

.dropdown .menu.is-active {
  display: block;
}

.skip-menu {
  list-style: none;
  position: absolute;
}

.skip-menu a {
  color: var(--color-secondary-01, #fff);
  position: absolute;
  top: -200em;
  white-space: nowrap;
}

.skip-menu a:focus {
  align-items: center;
  background: var(--color-highlight, #268744);
  border-radius: 100px;
  display: inline-flex;
  height: 44px;
  left: 20px;
  outline: 0;
  padding: 0 24px;
  position: absolute;
  text-decoration: none;
  top: auto;
  z-index: 11;
}

@supports (display: grid) {
  .template-system,
  .template-site,
  .template-authentication {
    display: grid;
    grid-template-areas: "header" "wrapper" "footer";
    grid-template-rows: auto 1fr auto;
    min-height: 100vh;
  }
  .template-system .page-header,
  .template-site .page-header,
  .template-authentication .page-header {
    grid-area: header;
  }
  .template-system .page-wrapper,
  .template-site .page-wrapper,
  .template-authentication .page-wrapper {
    grid-area: wrapper;
  }
  .template-system .page-footer,
  .template-site .page-footer,
  .template-authentication .page-footer {
    grid-area: footer;
  }
  .template-system .page-wrapper {
    display: grid;
    grid-template-columns: auto 1fr;
  }
}

